import React from "react";
import s from "./FormContorls.module.css"
import {requiredField} from "../Validators/validators";
import {Field} from "redux-form";

const FormControl = ({input, meta:{touched, error}, children}) => {
    const hasError = touched && error
    return (
        <div className={`${s.formControl} + ${hasError && s.error}`}>
            <div>
                {children}
            </div>
            {hasError && <span>
                {error}
            </span>}
        </div>
    )
}


export const Textarea = (props) => {
    const {input, meta, child, ...restProps} = props
    return <FormControl {...props} ><textarea {...input}{...restProps} /> </FormControl>            ///textarea child
    //Form Control ÐºÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€
}                                                                               //Ñ€ÐµÑ„Ð°ÐºÑ‚Ð¾Ñ€ Ð¸Ð· Ð˜Ð½Ð¿ÑƒÑ‚Ð° Ð¸ textArea Ð² Ð¾Ð´Ð½Ñƒ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚Ñƒ
export const Input = (props) => {
    const {input, meta, child, ...restProps} = props
    return <FormControl {...props} ><input {...input}{...restProps} /> </FormControl>
}

export const createField = (placeholder, name, validators, component, props= {},text ="") => (
    <div>
        <Field placeholder={placeholder} name={name}
               component={component}
               validate={validators}
               {...props}
        /> {text}
    </div>
)